import React, { useState, useEffect, useRef } from 'react'
import { View, Text, Image, ScrollView } from '@tarojs/components'
import Taro, { useRouter } from '@tarojs/taro'
import { orderService, CreateOrderParams } from '@/services/order'
import { storeService } from '@/services/store'
import { therapistService } from '@/services/therapist'
import { walletService } from '@/services/wallet.service'
import { paymentService } from '@/services/payment.service'
import './index.scss'

interface CartItem {
  serviceId: string
  serviceName: string
  duration: number
  price: number
  discountPrice?: number
  date: string
  time: string
  therapistId?: string
  therapistName: string
  therapistAvatar?: string
}

interface OrderConfirmPageParams {
  therapistId?: string
  storeId: string
  items: string // JSON string of CartItem[]
  from?: string  // Êù•Ê∫êÊ†áËØÜÔºåÂ¶Ç 'symptom'
}

const OrderConfirmPage: React.FC = () => {
  const router = useRouter()
  const params = router.params as unknown as OrderConfirmPageParams
  
  const [cartItems, setCartItems] = useState<CartItem[]>([])
  const [therapistInfo, setTherapistInfo] = useState<any>(null)
  const [storeInfo, setStoreInfo] = useState<any>(null)
  const [loading, setLoading] = useState(true)
  const [countdown, setCountdown] = useState(180) // 3ÂàÜÈíüÂÄíËÆ°Êó∂
  const [paymentMethod, setPaymentMethod] = useState<'wechat' | 'balance'>('wechat')
  const [userBalance, setUserBalance] = useState(0) // Áî®Êà∑‰ΩôÈ¢ù
  const [balanceLoading, setBalanceLoading] = useState(false)
  const timerRef = useRef<any>(null)

  useEffect(() => {
    // Ëß£Êûê‰º†ÈÄíÁöÑÊï∞ÊçÆ
    try {
      const items = JSON.parse(decodeURIComponent(params.items || '[]'))
      setCartItems(items)

      // Ëé∑ÂèñÊé®ÊãøÂ∏àÂíåÈó®Â∫ó‰ø°ÊÅØ
      fetchTherapistAndStoreInfo()
      // Ëé∑ÂèñÁî®Êà∑‰ΩôÈ¢ù
      fetchUserBalance()
    } catch (error) {
      Taro.showToast({
        title: 'Êï∞ÊçÆËß£ÊûêÂ§±Ë¥•',
        icon: 'none'
      })
      setTimeout(() => Taro.navigateBack(), 1500)
    }
  }, [params])

  // ÂÄíËÆ°Êó∂ÈÄªËæë
  useEffect(() => {
    if (!loading && cartItems.length > 0) {
      timerRef.current = setInterval(() => {
        setCountdown((prev) => {
          if (prev <= 1) {
            clearInterval(timerRef.current)
            Taro.showModal({
              title: 'ÊîØ‰ªòË∂ÖÊó∂‰∫ÜÂë¶',
              content: 'Âø´Âø´ÈáçÊñ∞‰∏ãÂçïÂêß~',
              showCancel: false,
              success: () => {
                Taro.navigateBack()
              }
            })
            return 0
          }
          return prev - 1
        })
      }, 1000)
    }

    return () => {
      if (timerRef.current) {
        clearInterval(timerRef.current)
      }
    }
  }, [loading, cartItems])

  // Ëé∑ÂèñÁî®Êà∑‰ΩôÈ¢ù
  const fetchUserBalance = async () => {
    try {
      setBalanceLoading(true)
      const balance = await walletService.getBalance()
      setUserBalance(balance)

      // Â¶ÇÊûú‰ΩôÈ¢ùÂÖÖË∂≥ÔºåÈªòËÆ§ÈÄâÊã©‰ΩôÈ¢ùÊîØ‰ªò
      const totalPrice = getTotalPrice()
      if (balance >= totalPrice / 100) { // ËΩ¨Êç¢‰∏∫ÂÖÉÊØîËæÉ
        setPaymentMethod('balance')
      }
    } catch (error) {
      console.error('Ëé∑Âèñ‰ΩôÈ¢ùÂ§±Ë¥•:', error)
      setUserBalance(0)
    } finally {
      setBalanceLoading(false)
    }
  }

  const fetchTherapistAndStoreInfo = async () => {
    try {
      setLoading(true)

      // Ëé∑ÂèñÊé®ÊãøÂ∏à‰ø°ÊÅØÔºà‰ªÖÂú®ÊúâtherapistIdÊó∂Ëé∑ÂèñÔºâ
      if (params.therapistId) {
        const therapistRes = await therapistService.getTherapistDetail(params.therapistId)
        setTherapistInfo(therapistRes.data)
      }

      // Ëé∑ÂèñÈó®Â∫ó‰ø°ÊÅØ
      const storeRes = await storeService.getStoreDetail(params.storeId)
      const storeData = storeRes.data

      setStoreInfo(storeData)
      setLoading(false)
    } catch (error) {
      setLoading(false)
      Taro.showToast({
        title: 'Ëé∑Âèñ‰ø°ÊÅØÂ§±Ë¥•',
        icon: 'none'
      })
    }
  }

  const formatCountdown = (seconds: number) => {
    const mins = Math.floor(seconds / 60)
    const secs = seconds % 60
    return `${mins.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`
  }

  const formatDate = (dateStr: string) => {
    const date = new Date(dateStr)
    const month = date.getMonth() + 1
    const day = date.getDate()
    return `${month.toString().padStart(2, '0')}Êúà${day.toString().padStart(2, '0')}Êó•`
  }

  const calculateEndTime = (time: string, duration: number) => {
    const [hour, minute] = time.split(':').map(Number)
    const endMinute = minute + duration
    const endHour = hour + Math.floor(endMinute / 60)
    const finalMinute = endMinute % 60
    return `${endHour}:${finalMinute.toString().padStart(2, '0')}`
  }

  const getTotalPrice = () => {
    return cartItems.reduce((sum, item) => sum + (item.discountPrice || item.price), 0)
  }

  // Ê£ÄÊü•‰ΩôÈ¢ùÊòØÂê¶ÂÖÖË∂≥
  const isBalanceSufficient = () => {
    const totalPrice = getTotalPrice()
    return userBalance >= totalPrice / 100 // ËΩ¨Êç¢‰∏∫ÂÖÉÊØîËæÉ
  }

  // Â§ÑÁêÜÊîØ‰ªòÊñπÂºèÂàáÊç¢
  const handlePaymentMethodChange = (method: 'wechat' | 'balance') => {
    if (method === 'balance' && !isBalanceSufficient()) {
      Taro.showToast({
        title: '‰ΩôÈ¢ù‰∏çË∂≥ÔºåËØ∑ÂÖÖÂÄºÊàñ‰ΩøÁî®ÂÖ∂‰ªñÊîØ‰ªòÊñπÂºè',
        icon: 'none',
        duration: 2000
      })
      return
    }
    setPaymentMethod(method)
  }

  const handlePayment = async () => {
    // ÁóáÁä∂Ë∞ÉÁêÜÊ®°Âºè‰∏ãÊé®ÊãøÂ∏à‰ø°ÊÅØÂú®cartItems‰∏≠Ôºå‰∏çÈúÄË¶ÅtherapistInfo
    const isSymptomMode = params.from === 'symptom'
    const needTherapistInfo = !isSymptomMode && !therapistInfo

    if (cartItems.length === 0 || needTherapistInfo || !storeInfo) {
      Taro.showToast({
        title: 'ËÆ¢Âçï‰ø°ÊÅØ‰∏çÂÆåÊï¥',
        icon: 'none'
      })
      return
    }

    // ‰ΩôÈ¢ùÊîØ‰ªòÂâçÂÜçÊ¨°Ê£ÄÊü•‰ΩôÈ¢ù
    if (paymentMethod === 'balance' && !isBalanceSufficient()) {
      Taro.showToast({
        title: '‰ΩôÈ¢ù‰∏çË∂≥ÔºåËØ∑ÂÖÖÂÄºÊàñ‰ΩøÁî®ÂÖ∂‰ªñÊîØ‰ªòÊñπÂºè',
        icon: 'none',
        duration: 2000
      })
      return
    }

    try {
      Taro.showLoading({
        title: 'ÂàõÂª∫ËÆ¢Âçï...'
      })

      // ‰ΩøÁî®Á¨¨‰∏Ä‰∏™Ë¥≠Áâ©È°πÁöÑ‰ø°ÊÅØÔºàÂ¶ÇÊûúÊúâÂ§ö‰∏™ÊúçÂä°ÔºåÂèØ‰ª•ÂêéÁª≠‰ºòÂåñÔºâ
      const firstItem = cartItems[0]

      // Ë∞ÉËØïÊó•Âøó - Êü•ÁúãË¥≠Áâ©ËΩ¶È°πÁõÆÊï∞ÊçÆ
      console.log('üõí Ë¥≠Áâ©ËΩ¶Á¨¨‰∏Ä‰∏™È°πÁõÆ:', firstItem)
      console.log('üõí firstItem.therapistId:', firstItem.therapistId)
      console.log('üõí params.therapistId:', params.therapistId)
      console.log('üõí params.from:', params.from)

      const orderParams: CreateOrderParams = {
        therapistId: firstItem.therapistId || params.therapistId || 'symptom-mode', // ‰ºòÂÖà‰ΩøÁî®Ë¥≠Áâ©ËΩ¶‰∏≠ÁöÑÊäÄÂ∏àID
        storeId: params.storeId,
        serviceId: firstItem.serviceId,
        serviceName: firstItem.serviceName,
        duration: firstItem.duration,
        price: firstItem.price,
        discountPrice: firstItem.discountPrice,
        appointmentDate: firstItem.date,
        appointmentTime: firstItem.time,
        therapistName: firstItem.therapistName,
        therapistAvatar: firstItem.therapistAvatar || (therapistInfo?.avatar)
      }

      // Ë∞ÉËØïÊó•Âøó - Êü•ÁúãÊúÄÁªàÁöÑËÆ¢ÂçïÂèÇÊï∞
      console.log('üì¶ ÊúÄÁªàÁöÑËÆ¢ÂçïÂèÇÊï∞:', orderParams)
      console.log('üì¶ therapistIdÂ∞ÜË¶Å‰º†ÈÄíÁöÑÂÄº:', orderParams.therapistId)

      // ÂàõÂª∫ËÆ¢Âçï
      const result = await orderService.createAppointmentOrder(orderParams)
      const order = result.order

      console.log('‚úÖ ËÆ¢ÂçïÂàõÂª∫ÊàêÂäü:', result)
      console.log('‚úÖ ËÆ¢ÂçïÂè∑:', order.orderNo)

      Taro.hideLoading()

      // Ê†πÊçÆÊîØ‰ªòÊñπÂºèË∞ÉÁî®‰∏çÂêåÁöÑÊîØ‰ªòÊñπÊ≥ï
      const paymentSuccess = await paymentService.pay({
        orderNo: order.orderNo,
        amount: order.totalAmount ? order.totalAmount * 100 : getTotalPrice() * 100, // ËΩ¨Êç¢‰∏∫ÂàÜ
        paymentMethod: paymentMethod,
        title: `${firstItem.serviceName} - ${firstItem.therapistName}`
      })

      if (paymentSuccess) {
        // ÊîØ‰ªòÊàêÂäüÂêéÊõ¥Êñ∞‰ΩôÈ¢ùÊòæÁ§∫
        if (paymentMethod === 'balance') {
          await fetchUserBalance()
        }

        setTimeout(() => {
          Taro.redirectTo({
            url: `/pages/booking/success/index?orderNo=${order.orderNo}`
          })
        }, 1500)
      } else {
        // Áîü‰∫ßÁéØÂ¢ÉË∞ÉÁî®ÁúüÂÆûÊîØ‰ªò
        Taro.requestPayment({
          ...paymentParams,
          success: async () => {
            // Êõ¥Êñ∞ËÆ¢ÂçïÁä∂ÊÄÅ‰∏∫Â∑≤ÊîØ‰ªò
            await orderService.updateOrderStatus(order.orderNo, 'paid')
            
            Taro.showToast({
              title: 'ÊîØ‰ªòÊàêÂäü',
              icon: 'success',
              duration: 1500
            })
            
            setTimeout(() => {
              Taro.redirectTo({
                url: `/pages/booking/success/index?orderNo=${order.orderNo}`
              })
            }, 1500)
          },
          fail: (err) => {
            console.error('ÊîØ‰ªòÂ§±Ë¥•:', err)
            if (err.errMsg !== 'requestPayment:fail cancel') {
              // Â¶ÇÊûúÊòØÁº∫Â∞ëtotal_feeÁöÑÈîôËØØÔºåÁªôÂá∫Êõ¥ÊòéÁ°ÆÁöÑÊèêÁ§∫
              if (err.errMsg && err.errMsg.includes('total_fee')) {
                Taro.showToast({
                  title: 'ÊîØ‰ªòÂèÇÊï∞ÈîôËØØÔºöÁº∫Â∞ëÈáëÈ¢ù‰ø°ÊÅØ',
                  icon: 'none',
                  duration: 2500
                })
              } else {
                Taro.showToast({
                  title: 'ÊîØ‰ªòÂ§±Ë¥•',
                  icon: 'none'
                })
              }
            }
          }
        })
      }
    } catch (error) {
      Taro.hideLoading()
      Taro.showToast({
        title: error.message || 'ËÆ¢ÂçïÂàõÂª∫Â§±Ë¥•',
        icon: 'none'
      })
    }
  }

  if (loading) {
    return (
      <View className="order-confirm-page">
        <View className="loading">Âä†ËΩΩ‰∏≠...</View>
      </View>
    )
  }

  return (
    <ScrollView className="order-confirm-page" scrollY>
      {/* Èó®Â∫ó‰ø°ÊÅØ */}
      <View className="store-section">
        <Text className="store-name">{storeInfo?.name}</Text>
        <Text className="store-distance">üìç {storeInfo?.distance}km</Text>
      </View>

      {/* È¢ÑÁ∫¶‰ø°ÊÅØ */}
      <View className="booking-info">
        {cartItems.map((item, index) => (
          <View key={index} className="booking-item">
            <Image 
              className="therapist-avatar" 
              src={item.therapistAvatar || therapistInfo?.avatar} 
            />
            <View className="booking-details">
              <View className="therapist-name">{item.therapistName}</View>
              <View className="service-time">
                {formatDate(item.date)} {item.time} Ëá≥ {calculateEndTime(item.time, item.duration)}
              </View>
              <View className="service-name">{item.serviceName}</View>
            </View>
            <View className="service-price">¬•{item.discountPrice || item.price}</View>
          </View>
        ))}
      </View>

      {/* ÈÄÄÂçïËØ¥Êòé */}
      <View className="refund-policy">
        <Text className="policy-title">ÈÄÄÂçïËØ¥Êòé</Text>
        <View className="policy-list">
          <View className="policy-item">
            ‚Ä¢ ‰∏ãÂçï15ÂàÜÈíüÂÜÖÊàñË∑ùËÆ¢ÂçïÂºÄÂßãÊó∂Èó¥&gt;6Â∞èÊó∂ÈÄÄÂçïÔºåÈÄÄ100%
          </View>
          <View className="policy-item">
            ‚Ä¢ Ë∑ùËÆ¢ÂçïÂºÄÂßãÂâç&lt;6Â∞èÊó∂ÈÄÄÂçïÔºåÈÄÄÂÆû‰ªòÈáëÈ¢ù90%
          </View>
          <View className="policy-item">
            ‚Ä¢ ËÆ¢ÂçïÊó∂Èó¥ÂºÄÂßãÂêéÈÄÄÂçïÔºåÈÄÄÂÆû‰ªòÈáëÈ¢ù80%
          </View>
        </View>
      </View>

      {/* ÂÆ¢Êà∑Â§áÊ≥® */}
      <View className="customer-note">
        <Text className="note-title">ÂÆ¢Êà∑Â§áÊ≥®</Text>
        <Text className="note-hint">ÊÇ®ÂØπËå∂Ê∞¥„ÄÅÊàøÈó¥„ÄÅÊåâÊë©ÊúçÁ≠âÊòØÂê¶ÊúâÁâπÊÆäÈúÄÊ±ÇÔºåÊàë‰ª¨Â∞ÜÊèêÂâç‰∏∫ÊÇ®ÂÅöÂ•ΩÂáÜÂ§á</Text>
      </View>

      {/* ÊîØ‰ªòÊñπÂºè */}
      <View className="payment-section">
        <Text className="section-title">ÊîØ‰ªòÊñπÂºè</Text>
        <View className="payment-methods">
          {/* ‰ΩôÈ¢ùÊîØ‰ªò */}
          <View
            className={`payment-method ${paymentMethod === 'balance' ? 'active' : ''} ${!isBalanceSufficient() ? 'disabled' : ''}`}
            onClick={() => handlePaymentMethodChange('balance')}
          >
            <View className="method-info">
              <Text className="method-icon">üí∞</Text>
              <Text className="method-name">‰ΩôÈ¢ùÊîØ‰ªò</Text>
              <Text className="balance-amount">
                {balanceLoading ? 'Âä†ËΩΩ‰∏≠...' : `¬•${userBalance.toFixed(2)}`}
                {!isBalanceSufficient() && !balanceLoading && (
                  <Text className="insufficient"> (‰ΩôÈ¢ù‰∏çË∂≥)</Text>
                )}
              </Text>
            </View>
            <View className={`check-icon ${paymentMethod === 'balance' ? 'checked' : ''}`} />
          </View>

          {/* ÂæÆ‰ø°ÊîØ‰ªò */}
          <View
            className={`payment-method ${paymentMethod === 'wechat' ? 'active' : ''}`}
            onClick={() => handlePaymentMethodChange('wechat')}
          >
            <View className="method-info">
              <Text className="method-icon">üíö</Text>
              <Text className="method-name">ÂæÆ‰ø°ÊîØ‰ªò</Text>
            </View>
            <View className={`check-icon ${paymentMethod === 'wechat' ? 'checked' : ''}`} />
          </View>
        </View>
      </View>

      {/* Â∫ïÈÉ®ÊîØ‰ªòÊ†è */}
      <View className="payment-bar">
        <View className="price-info">
          <Text className="total-price">¬• {getTotalPrice()}</Text>
          <Text className="countdown">ÊîØ‰ªòÂÄíËÆ°Êó∂: {formatCountdown(countdown)}</Text>
        </View>
        <View className="pay-button" onClick={handlePayment}>
          ÂéªÊîØ‰ªò
        </View>
      </View>
    </ScrollView>
  )
}

export default OrderConfirmPage