/**
 * ÊîØ‰ªòÊúçÂä°
 * ÊîØÊåÅÊ®°ÊãüÊîØ‰ªòÂíå‰ΩôÈ¢ùÊîØ‰ªòÔºà‰∏™‰∫∫Â∞èÁ®ãÂ∫èÂèØÁî®Ôºâ
 */
import Taro from '@tarojs/taro'
import { post } from '@/utils/request'

interface PaymentConfig {
  useMockPayment: boolean
  enableBalancePayment: boolean
  enableWechatPayment: boolean
}

interface PaymentOptions {
  orderNo: string
  amount: number
  paymentMethod: 'wechat' | 'balance' | 'mock'
  title?: string
}

class PaymentService {
  private config: PaymentConfig = {
    // ‰∏™‰∫∫Â∞èÁ®ãÂ∫èÈªòËÆ§ÈÖçÁΩÆ
    useMockPayment: true,
    enableBalancePayment: true,
    enableWechatPayment: false
  }

  /**
   * Áªü‰∏ÄÊîØ‰ªòÂÖ•Âè£
   */
  async pay(options: PaymentOptions): Promise<boolean> {
    const { paymentMethod } = options

    // ÂºÄÂèëÁéØÂ¢ÉÊàñ‰∏™‰∫∫Â∞èÁ®ãÂ∫è‰ΩøÁî®Ê®°ÊãüÊîØ‰ªò
    if (this.config.useMockPayment && paymentMethod === 'wechat') {
      return this.mockWechatPayment(options)
    }

    // ‰ΩôÈ¢ùÊîØ‰ªò
    if (paymentMethod === 'balance') {
      return this.payWithBalance(options)
    }

    // ÁúüÂÆûÂæÆ‰ø°ÊîØ‰ªòÔºàÈúÄË¶Å‰ºÅ‰∏öËÆ§ËØÅÔºâ
    if (paymentMethod === 'wechat' && this.config.enableWechatPayment) {
      return this.payWithWechat(options)
    }

    throw new Error('‰∏çÊîØÊåÅÁöÑÊîØ‰ªòÊñπÂºè')
  }

  /**
   * Ê®°ÊãüÂæÆ‰ø°ÊîØ‰ªòÔºà‰∏™‰∫∫Â∞èÁ®ãÂ∫èÊµãËØïÁî®Ôºâ
   */
  private async mockWechatPayment(options: PaymentOptions): Promise<boolean> {
    try {
      // ÊòæÁ§∫Ê®°ÊãüÊîØ‰ªòÁïåÈù¢
      const { confirm } = await Taro.showModal({
        title: 'Ê®°ÊãüÊîØ‰ªò',
        content: `ËÆ¢ÂçïÈáëÈ¢ùÔºö¬•${(options.amount / 100).toFixed(2)}\n${options.title || ''}`,
        confirmText: 'Á°ÆËÆ§ÊîØ‰ªò',
        cancelText: 'ÂèñÊ∂àÊîØ‰ªò',
        confirmColor: '#07c160'
      })

      if (confirm) {
        // ÊòæÁ§∫Âä†ËΩΩÂä®Áîª
        Taro.showLoading({ title: 'ÊîØ‰ªò‰∏≠...' })

        // Ê®°ÊãüÁΩëÁªúÂª∂Ëøü
        await this.delay(1500)

        // Ë∞ÉÁî®ÂêéÁ´ØÊõ¥Êñ∞ËÆ¢ÂçïÁä∂ÊÄÅ
        await post('/orders/mock-pay', {
          orderNo: options.orderNo,
          paymentStatus: 'paid'
        })

        Taro.hideLoading()
        Taro.showToast({
          title: 'ÊîØ‰ªòÊàêÂäü',
          icon: 'success'
        })

        return true
      } else {
        console.log('Áî®Êà∑ÂèñÊ∂àÊ®°ÊãüÊîØ‰ªò')
        return false
      }
    } catch (error) {
      Taro.hideLoading()
      Taro.showToast({
        title: 'ÊîØ‰ªòÂ§±Ë¥•',
        icon: 'none'
      })
      throw error
    }
  }

  /**
   * ‰ΩôÈ¢ùÊîØ‰ªò
   */
  private async payWithBalance(options: PaymentOptions): Promise<boolean> {
    try {
      Taro.showLoading({ title: 'ÊîØ‰ªò‰∏≠...' })

      console.log('üí∞ ‰ΩôÈ¢ùÊîØ‰ªòËØ∑Ê±ÇÂèÇÊï∞:', {
        orderNo: options.orderNo,
        paymentMethod: 'balance'
      })

      // Ë∞ÉÁî®‰ΩôÈ¢ùÊîØ‰ªòÊé•Âè£
      const response = await post('/orders/pay', {
        orderNo: options.orderNo,
        paymentMethod: 'balance'
      })

      console.log('üí∞ ‰ΩôÈ¢ùÊîØ‰ªòÂìçÂ∫î:', response)

      Taro.hideLoading()

      if (response.code === 0) {
        // ÊòæÁ§∫Êñ∞‰ΩôÈ¢ù
        Taro.showToast({
          title: `ÊîØ‰ªòÊàêÂäü\n‰ΩôÈ¢ùÔºö¬•${(response.data.balance / 100).toFixed(2)}`,
          icon: 'success',
          duration: 2000
        })
        return true
      } else {
        throw new Error(response.message || '‰ΩôÈ¢ù‰∏çË∂≥')
      }
    } catch (error: any) {
      console.error('üí∞ ‰ΩôÈ¢ùÊîØ‰ªòÂ§±Ë¥•:', error)
      console.error('üí∞ ÈîôËØØËØ¶ÊÉÖ:', error.response || error.message)
      Taro.hideLoading()
      Taro.showToast({
        title: error.message || 'ÊîØ‰ªòÂ§±Ë¥•',
        icon: 'none'
      })
      return false
    }
  }

  /**
   * ÁúüÂÆûÂæÆ‰ø°ÊîØ‰ªòÔºàÈúÄË¶Å‰ºÅ‰∏öËÆ§ËØÅÔºâ
   */
  private async payWithWechat(options: PaymentOptions): Promise<boolean> {
    try {
      // Ëé∑ÂèñÊîØ‰ªòÂèÇÊï∞
      const { data } = await post('/orders/wechat-pay-params', {
        orderNo: options.orderNo
      })

      const { wxPayParams } = data

      // Ë∞ÉËµ∑ÂæÆ‰ø°ÊîØ‰ªò
      await Taro.requestPayment({
        timeStamp: wxPayParams.timeStamp,
        nonceStr: wxPayParams.nonceStr,
        package: wxPayParams.package,
        signType: wxPayParams.signType as any,
        paySign: wxPayParams.paySign
      })

      Taro.showToast({
        title: 'ÊîØ‰ªòÊàêÂäü',
        icon: 'success'
      })

      return true
    } catch (error: any) {
      if (error.errMsg === 'requestPayment:fail cancel') {
        console.log('Áî®Êà∑ÂèñÊ∂àÊîØ‰ªò')
        return false
      }

      Taro.showToast({
        title: 'ÊîØ‰ªòÂ§±Ë¥•',
        icon: 'none'
      })
      throw error
    }
  }

  /**
   * Ê£ÄÊü•ÊîØ‰ªòÁéØÂ¢É
   */
  async checkPaymentEnvironment(): Promise<{
    canUseWechatPay: boolean
    canUseBalance: boolean
    canUseMockPay: boolean
    message: string
  }> {
    // Ëé∑ÂèñÂ∞èÁ®ãÂ∫èË¥¶Âè∑‰ø°ÊÅØ
    const accountInfo = Taro.getAccountInfoSync()
    const { miniProgram } = accountInfo

    // ‰∏™‰∫∫Â∞èÁ®ãÂ∫èÁöÑAppIDÈÄöÂ∏∏‰ª•wxÂºÄÂ§¥ÁöÑ‰∏™‰∫∫Á±ªÂûã
    const isPersonalApp = !this.config.enableWechatPayment

    return {
      canUseWechatPay: !isPersonalApp && this.config.enableWechatPayment,
      canUseBalance: this.config.enableBalancePayment,
      canUseMockPay: this.config.useMockPayment,
      message: isPersonalApp
        ? 'ÂΩìÂâç‰∏∫‰∏™‰∫∫Â∞èÁ®ãÂ∫èÔºå‰ΩøÁî®Ê®°ÊãüÊîØ‰ªòÂíå‰ΩôÈ¢ùÊîØ‰ªò'
        : '‰ºÅ‰∏öÂ∞èÁ®ãÂ∫èÔºåÊîØÊåÅÂÆåÊï¥ÊîØ‰ªòÂäüËÉΩ'
    }
  }

  /**
   * ÁîüÊàêÂÖÖÂÄºÁ†ÅÔºàÁ∫ø‰∏ãÂÖÖÂÄºÔºâ
   */
  async generateRechargeCode(amount: number): Promise<{
    code: string
    qrcode: string
    expireTime: string
  }> {
    const response = await post('/recharge/generate-code', { amount })
    return response.data
  }

  /**
   * ËæÖÂä©ÊñπÊ≥ïÔºöÂª∂Ëøü
   */
  private delay(ms: number): Promise<void> {
    return new Promise(resolve => setTimeout(resolve, ms))
  }
}

// ÂØºÂá∫Âçï‰æã
export const paymentService = new PaymentService()

// ÂØºÂá∫ÊîØ‰ªòÊñπÂºèÊûö‰∏æ
export enum PaymentMethod {
  WECHAT = 'wechat',
  BALANCE = 'balance',
  MOCK = 'mock'
}